<template>
  <div>
    <div>home</div>
    {{ loading }}
    <van-tabs v-model="active" :before-change="beforeChange">
      <van-tab :title="item" v-for="item in tabs" :key="item">
        <van-list :immediate-check="false" v-model="loading" @load="onLoad" offset="100">
          <div style="height: calc(100vh + 100px); background: #ccc"></div>
        </van-list>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      active: 0,
      loading: false,
      tabs: ["a", "b"],
    };
  },
  watch: {},
  created() {},
  methods: {
    onLoad() {
      console.error("onLoad");
      setTimeout(() => {
        console.error("false");
        this.loading = false;
      }, 2000);
    },
    beforeChange(index) {
      this.loading = false;
      // 返回 Promise 来执行异步逻辑
      return new Promise((resolve) => {
        // 在 resolve 函数中返回 true 或 false
        resolve(true);
      });
    },
  },
};
</script>

<style lang="scss" scoped></style>
